.cookie-notice {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  color: #82f0d7;
  padding: 1.5em 0;
  opacity: 0;
  transition: opacity 0.3s ease-in-out; }
  .cookie-notice.active {
    display: block; }
  .cookie-notice.animate {
    opacity: 1; }
  .cookie-notice .cookie-notice-wrapper {
    position: relative;
    max-width: 680px;
    margin: 0 auto;
    padding: 1em 1.25em; }
    .cookie-notice .cookie-notice-wrapper:before, .cookie-notice .cookie-notice-wrapper:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 84%;
      left: 0;
      z-index: -1; }
    .cookie-notice .cookie-notice-wrapper:before {
      border-left: 3px solid #82f0d7;
      border-top: 3px solid #82f0d7;
      top: 0; }
    .cookie-notice .cookie-notice-wrapper:after {
      border-right: 3px solid #82f0d7;
      border-bottom: 3px solid #82f0d7;
      bottom: 0; }
  .cookie-notice .cookie-notice-close {
